@import '../../../../../assets/css/var.scss';
@import '../../../../../assets/css/theme';

.v3-pool-list {
  width: 90%;
  margin: 50px auto 0;
  max-width: 1200px;
  .v3-ques-png {
    width: 16px;
    height: 16px;
    margin-left: 10px;
    cursor: pointer;
  }
  .pool-tilte {
    font-size: 20px;
    font-weight: bold;
    color: $color-text-default;
    line-height: 24px;
  }
  .pool-setting {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default-5;
    cursor: pointer;
  }
  .pool-balance {
    :nth-child(1) {
      font-size: 14px;
      font-family: PingFangSC, PingFang SC;
      font-weight: 400;
      color: $color-text-default-5;
      line-height: 20px;
    }
    :nth-child(2) {
      font-size: 20px;
      font-family: PingFangSC, PingFang SC;
      font-weight: 600;
      color: $color-text-default;
      line-height: 28px;
    }
  }

  .ant-switch {
    margin-left: 12px;
  }
  .ant-switch-inner {
    background: #cccccc;
  }
  .ant-switch-checked {
    .ant-switch-inner {
      background: $color-primary;
    }
  }

  .err-list {
    color: #bbb;
    padding: 50px 0;
    text-align: center;
  }

  .wallet-link {
    display: inline-flex;
    align-items: center;
    padding: 12px 30px;
    color: $color-a-default !important;
    background: #c3a6fa;
    cursor: pointer;
    border-radius: 10px;

    > span {
      line-height: 16px;
      margin-left: 8px;
      margin-right: 38px;
      text-align: left;
    }

    > img {
      &:first-child {
        width: 17px;
        height: 17px;
      }

      &:last-child {
        width: 14px;
        height: 8px;
      }
    }
  }
  .sun-swap-empty-text {
    margin-top: 16px;
  }
  .v3-pools-margin {
    > div {
      margin-bottom: 12px;
    }
  }
}
